*{
  box-sizing:border-box;
  padding: 0;
  margin: 0;
  
}
.header{
  width: 100%;
  line-height: 100px;
  background-color: #f5ba13;
  display: flex;
  justify-content: left;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.3);
}
.main_note{
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
  box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.5);
 
}
.my_notes h1{
  color: #34495e;
}
.my_notes p{
  color: grey;
}
.form{
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.5);
  border-radius: 10px;
  position: relative;
}
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;;
  outline-style: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}
textarea{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline-style: none;
  
  font-size: 1rem;
  margin: 10px 0;
}

.MuiButton-root{
width: 30px;
height: 50px;
position: absolute;
bottom: -28px;
left: 400px;
cursor: pointer;
background-color: #fff !important;
border-radius: 50% !important;
}
.MuiButton-root .plus-sign{
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b000;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}
.MuiButton-root:hover{
  background-color: #f4b400;
}
.MuiButton-root:hover .plus-sign{
  color: #fff;
  background: transparent;
}
.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px;
  padding: 10px;
  width: 240px;
  margin: 16px;
 float: left;
 box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.5);
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  border: none;
  width: 50px;
 color:#f4b400 ;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  border-radius: 50%;

}
.deleteIcon{
  width: 0.7em;
  height: 0.7em;
 
}
.note .btn:hover{
  background-color: #f5ba13;
}
.note .btn:hover .deleteIcon{
  color: #fff;
}
footer{
  width : 100%;
  margin-top:10px;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
 
  font-size: 20px;
}

.plus_sign{
  color: #f4b000;
}